import { types } from "../types/types";

const initialState = {
    modalContact: false,
    modalCollection: false,
    currentImage: null
}

export const uiReducer = ( state=initialState, action ) => {
    switch (action.type) {
        case types.uiOpenModal:
            return {
                ...state,
                modalContact: true
            }

        case types.uiCloseModal:
            return {
                ...state,
                modalContact: false
            }

        case types.uiOpenModalCollection:
            return {
                ...state,
                modalCollection: true,
                currentImage: action.payload
            }

        case types.uiCloseModalCollection:
            return {
                ...state,
                modalCollection: false,
                currentImage: null
            }

        default:
            return state;
    }
}
