import React from 'react'
import { Link } from 'react-router-dom'

export const NavResponsive = () => {
    return (
        <>
            <div className="navbar navbar-light navbar-bg fixed-top">
                <div className="container-fluid">
                    <Link 
                        className="navbar-brand bolder gray"
                        to="/"
                    >
                        Jean Victor Rada
                    </Link>

                    <button type="button" className="btn-nav__responsive" data-bs-toggle="offcanvas" data-bs-target="#offcanvasRight" aria-controls="offcanvasRight">
                        <i className="fas fa-bars"></i>
                    </button>

                    <div className="offcanvas offcanvas-end" tabIndex="-1" id="offcanvasRight" aria-labelledby="offcanvasRightLabel">
                        <div className="offcanvas-header">
                            <div className="router-responsive">
                                <Link className="router-responsive__link" to="/folio">
                                    <h6 id="offcanvasRightLabel">
                                        Portafolio
                                    </h6>
                                </Link>
                                <Link className="router-responsive__link" to="/about">
                                    <h6 id="offcanvasRightLabel">
                                        About
                                    </h6>
                                </Link>
                            </div>
                            <button type="button" className="btn-close text-reset" data-bs-dismiss="offcanvas" aria-label="Close"></button>
                        </div>
                    </div>
                </div>
            </div>
        </>

    )
}
