import { types } from "../types/types";

export const _uiOpenModal = () => ({
    type: types.uiOpenModal
})

export const _uiCloseModal = () => ({
    type: types.uiCloseModal
})

export const _uiOpenModalCollection = ( url ) => ({
    type: types.uiOpenModalCollection,
    payload: url
})

export const _uiCloseModalCollection = () => ({
    type: types.uiCloseModalCollection
})