import React from 'react';
import './portafolio.css';
import { useMediaQuery } from 'react-responsive';
import Folio from '../../assets/folio/folio.jpg';
import { ContactResponsive } from '../ui/ContactResponsive';
import metamaskBanner from '../../assets/folio/metamaskbanner.jpg';
import metamaskExample1 from '../../assets/folio/metamaskexample.jpg';
import metamaskExample2 from '../../assets/folio/metamaskexample1.jpg';

import ansellBanner from '../../assets/folio/ansellbanner.jpg';
import ansellExample1 from '../../assets/folio/ansellexample.jpg';
import ansellExample2 from '../../assets/folio/ansellexample1.jpg';

import saludBanner from '../../assets/folio/saludbanner.jpg';
import saludExample1 from '../../assets/folio/saludexample.jpg';
import saludExample2 from '../../assets/folio/saludexample1.jpg';

import mernBanner from '../../assets/folio/mernbanner.jpg';
import mernExample1 from '../../assets/folio/mernexample.jpg';
import mernExample2 from '../../assets/folio/mernexample1.jpg';

import elbBanner from '../../assets/folio/elbbanner.jpg';
import elbExample1 from '../../assets/folio/elbexample.jpg';
import elbExample2 from '../../assets/folio/elbexample1.jpg';

import { useDispatch } from 'react-redux';
import { _uiOpenModalCollection } from '../../actions/uiActions';
import { PortafolioModal } from './PortafolioModal';

export const PortafolioScreen = () => {

    const dispatch = useDispatch();

    const isDesktop = useMediaQuery({
        query: '(min-width: 750px)'
      })

    const handleOpenModal = ({ target }) => {
        dispatch( _uiOpenModalCollection( target.src ) );
    }

    return (
           <>
                <div className={ isDesktop? 'folio-desktop__profile':'folio-responsive__profile' }>
                    <img src={ Folio } alt="Jean Victor Rada"/>
                    <h2>
                        My Works <br/>
                        Collection
                    </h2>
                </div>
                <div className={ isDesktop? 'folio-desktop__body': 'folio-responsive__body' }>
                    <div className={ isDesktop? 'folio-desktop__body-section': 'folio-responsive__body-section' }>
                        <img onClick={ handleOpenModal } src={ metamaskBanner } alt="Jean Victor Rada"/>
                        <h5>MetamaskApp-Make transactions easily and quickly.</h5>
                        <p>
                            This small web app was made with ReactJS, Bootstrap and Metamask API, in  order to perform a fast transfer service.
                            <br/>
                            <a className="card-link fs-5" href="https://jeansyo.github.io/metamask-app/">Link</a>
                        </p>
                    </div>
                    <div className={ isDesktop? 'folio-desktop__body-images': 'folio-responsive__body-images' }>
                        <img onClick={ handleOpenModal } className={ isDesktop ? 'img-left' : 'not-class'} src={ metamaskExample2 } alt="Jean Victor Rada"/>
                        <img onClick={ handleOpenModal } className={ isDesktop ? 'img-right': 'not-class' } src={ metamaskExample1 } alt="Jean Victor Rada"/>
                    </div>
                </div>
                <div className='folio-responsive__parallax parallax-border'>
                    <div className='folio-responsive__parallax-image'>
                    </div>
                </div>
                <div className={ isDesktop? 'folio-desktop__body': 'folio-responsive__body' }>
                    <div className={ isDesktop? 'folio-desktop__body-section': 'folio-responsive__body-section' }>
                        <img onClick={ handleOpenModal } src={ ansellBanner } alt="Jean Victor Rada"/>
                        <h5>
                            The glove of power (contest)
                            Ansell-Colombia
                        </h5>
                        <p>
                            This web page was made with the MERN stack.<br/>
                            The objective of this was for a contest carried out by the Ansell distributor in Colombia.
                        </p>
                    </div>
                    <div className={ isDesktop? 'folio-desktop__body-images': 'folio-responsive__body-images' }>
                        <img onClick={ handleOpenModal } className={ isDesktop ? 'img-left' : 'not-class'} src={ ansellExample1 } alt="Jean Victor Rada"/>
                        <img onClick={ handleOpenModal } className={ isDesktop ? 'img-right': 'not-class' } src={ ansellExample2 } alt="Jean Victor Rada"/>
                    </div>
                </div>
                <div className='folio-responsive__parallax'>
                    <div className='folio-responsive__parallax-image'>
                    </div>
                </div>
                <div className={ isDesktop? 'folio-desktop__body': 'folio-responsive__body' }>
                    <div className={ isDesktop? 'folio-desktop__body-section': 'folio-responsive__body-section' }>
                        <img onClick={ handleOpenModal } src={ saludBanner } alt="Jean Victor Rada"/>
                        <h5>
                            Yo Amo Mi Salud TV 
                            <br/>
                            Lima-Perú
                        </h5>
                        <p>
                            Collaboration with Yo Amo Mi Salud tv program in Perú, to make the layout of their website. This was done with HTML5, CSS3 and JS.
                            <br/>
                            <a className="card-link fs-5" href="https://www.yoamomisalud.tv/wb/">Link</a>
                        </p>
                    </div>
                    <div className={ isDesktop? 'folio-desktop__body-images': 'folio-responsive__body-images' }>
                        <img onClick={ handleOpenModal } className={ isDesktop ? 'img-left' : 'not-class'} src={ saludExample1 } alt="Jean Victor Rada"/>
                        <img onClick={ handleOpenModal } className={ isDesktop ? 'img-right': 'not-class' } src={ saludExample2 } alt="Jean Victor Rada"/>
                    </div>
                </div>
                <div className='folio-responsive__parallax parallax-border'>
                    <div className='folio-responsive__parallax-image'>
                    </div>
                </div>
                <div className={ isDesktop? 'folio-desktop__body': 'folio-responsive__body' }>
                    <div className={ isDesktop? 'folio-desktop__body-section': 'folio-responsive__body-section' }>
                        <img onClick={ handleOpenModal } src={ mernBanner } alt="Jean Victor Rada"/>
                        <h5>
                            Salud-App
                            <br/>
                            Personal project - MERN Stack
                        </h5>
                        <p>
                            This web application (beta) aims to demonstrate my skills with the MERN stack (MongoDB, Express, React, Node), my skills with pure css and a basic deployment of the front-end and backend in the cloud (AWS).
                            <br/>
                            <a className="card-link fs-5" href="http://salud-app-folio.s3-website-us-east-1.amazonaws.com/">Link</a>
                        </p>
                    </div>
                    <div className={ isDesktop? 'folio-desktop__body-images': 'folio-responsive__body-images' }>
                        <img onClick={ handleOpenModal } className={ isDesktop ? 'img-left' : 'not-class'} src={ mernExample1 } alt="Jean Victor Rada"/>
                        <img onClick={ handleOpenModal } className={ isDesktop ? 'img-right': 'not-class' } src={ mernExample2 } alt="Jean Victor Rada"/>
                    </div>
                </div>
                <div className='folio-responsive__parallax parallax-border'>
                    <div className='folio-responsive__parallax-image'>
                    </div>
                </div>
                <div className={ isDesktop? 'folio-desktop__body': 'folio-responsive__body' }>
                    <div className={ isDesktop? 'folio-desktop__body-section': 'folio-responsive__body-section' }>
                        <img onClick={ handleOpenModal } src={ elbBanner } alt="Jean Victor Rada"/>
                        <h5>AWS ELB<br/>Elastic Load Balancing.</h5>
                        <p>
                            This is a small website that aims to demonstrate my abilities with cloud technology.
                            <br/>
                            <a className="card-link fs-5" href="lb-EC2-ELB-890491498.us-east-1.elb.amazonaws.com">Link</a>
                        </p>
                    </div>
                    <div className={ isDesktop? 'folio-desktop__body-images': 'folio-responsive__body-images' }>
                        <img onClick={ handleOpenModal } className={ isDesktop ? 'img-left': 'not-class' } src={ elbExample2 } alt="Jean Victor Rada"/>
                        <img onClick={ handleOpenModal } className={ isDesktop ? 'img-right': 'not-class' } src={ elbExample1 } alt="Jean Victor Rada"/>
                    </div>
                </div>
                {
                    !isDesktop && 
                        <ContactResponsive/>
                }
                <PortafolioModal/>
            </> 
    )
}
