import React from 'react';
import About from '../../assets/about/aboutme.jpg';
import { useMediaQuery } from 'react-responsive';

import cv from '../../assets/about/cv.pdf';

import './about.css';
import { ContactResponsive } from '../ui/ContactResponsive';

export const AboutScreen = () => {

    const isDesktop = useMediaQuery({
        query: '(min-width: 750px)'
      })

    return (
        <>
            <div className="about-banner">
                <div className="about-image">
                    <h2>
                        About <br/>
                        Me
                    </h2>
                    <img src={ About } alt="Jean Victor Rada"/>
                </div>
                <div className='about-content about-info'>
                    <p>
                        Self-taught Web / MERN Stack developer looking for challenging problems to solve, my    experience is in the development of web applications remotely, however, I have a lot of    interest in AI especially in deep learning and cloud technology - Because of the potential     they have to change the technological landscape.
        ​               <br/><br/>
                        I like to write on Medium to help people who enter this world of technology to clarify their doubts.
                        <br/><br/>
                        Some of my hobbies are walking, exploring new places in the city where I live, I usually do it with colleagues from the university.
                    </p>
                    <button><a rel="noopener noreferrer" href={cv} target="_BLANK" download="JeanRada.pdf">Download CV</a></button>
                </div>
            </div>
            <div className="about-content about-touch">
                <h3>
                    Get In Touch
                </h3>
                <div className="about-touch__contact">
                    <div className="about-touch__contact-item">
                        <b>Contact</b>
                        <p>
                            radajean36@gmail.com
                            <br/>
                            Tel: +591 68022274
                        </p>
                    </div>
                    <div className="about-touch__contact-item">
                        <b>
                            Based In
                        </b>
                        <p>
                            La Paz, Bolivia
                        </p>
                    </div>
                </div>
            </div>
            {
                ( !isDesktop ) &&
                    (
                        <ContactResponsive/>
                    )
            }
        </>
    )
}
