import React from 'react';
import { Link } from 'react-router-dom';
import Image from '../../assets/home/profile.jpg';
import { ContactModal } from '../contact/ContactModal';
import { ContactResponsive } from '../ui/ContactResponsive';

export const HomeResponsive = () => {

    return (
        <>
            <div className="home-responsive">
                <h1>
                    Jean<br/>
                    Victor Rada
                    <pre>MERN Developer</pre>
                </h1>
                <div className="home-responsive__image">
                    <Link className='view-responsive' to="/folio">
                        View My Work
                    </Link>
                    <img src={ Image } alt="Jean Victor Rada"/>
                </div>
                <ContactResponsive/>
            </div>
            <ContactModal/>
        </>
    )
}
