import React from 'react';
import { Link, NavLink } from 'react-router-dom';
import { NavResponsive } from './NavResponsive';
import { useMediaQuery } from 'react-responsive';

import './navscreen.css';
import { useDispatch } from 'react-redux';
import { _uiOpenModal } from '../../actions/uiActions';
import { ContactModal } from '../contact/ContactModal';

export const NavScreen = () => {

    const dispatch = useDispatch();

    const isDesktop = useMediaQuery({
        query: '(min-width: 750px)'
      })

    const handleOpenModal = () => {
        dispatch( _uiOpenModal() )
    }

    return (
        <>
            {
                (isDesktop)
                    ?(
                        <div className="nav-desktop">
                            <div className="fixed nav-info ">
                                <Link className="nav-route bolder" to="/">
                                    Jean Victor Rada
                                </Link>
                                <NavLink className='nav-route' to="/folio" activeClassName="nav-active">
                                    Portafolio
                                </NavLink>
                                <NavLink className='nav-route' to="/about" activeClassName="nav-active">
                                    About
                                </NavLink>
                            </div>
                            <div className="fixed nav-contact ">
                                <a className='nav-route' href="mailto:radajean36@gmail.com">
                                    radajean36@gmail.com
                                </a>
                                <a className='nav-route' href="tel:+591 68022274">
                                    +591 68022274
                                </a>
                            </div>
                            <div className="fixed nav-social ">
                                <a target="_blank" className='nav-route'rel="noopener noreferrer" href="https://www.linkedin.com/in/jean-victor-rada-0427a1202/">
                                    Linkedin
                                </a>
                                <a target="_blank" className='nav-route' rel="noopener noreferrer" href="https://github.com/jeansyo">
                                    GitHub
                                </a>
                                <a target="_blank" className='nav-route' rel="noopener noreferrer" href="https://www.instagram.com/jeanrsyo/">
                                    Instagram
                                </a>
                                <a target='_blank' className='nav-route' rel="noopener noreferrer" href='https://www.kaggle.com/jeanrada/code'>
                                    Kaggle
                                </a>
                                <a target='_blank' className='nav-route' rel='noopener noreferrer' href='https://medium.com/@jeansyo'>
                                    Medium
                                </a>
                            </div>
                            <div onClick={ handleOpenModal } className="fixed nav-message pointer">
                                Get In Touch
                            </div>
                        </div>
                    )
                    :(
                        <NavResponsive/>
                    )
            }
            <ContactModal/>
        </>
    )
}
