import React from 'react';
import './contactresponsive.css';
import { useDispatch } from 'react-redux';
import { _uiOpenModal } from '../../actions/uiActions';

export const ContactResponsive = () => {

    
    const dispatch = useDispatch();
    const handleOnClick = () => {
        dispatch( _uiOpenModal() );
    }

    return (
        <div className="contact-responsive">
            <a href="www.gmail.com">
                radajean36@gmail.com
            </a>
            <p onClick={ handleOnClick } className="pointer">
                Get In Touch
            </p>
        </div>
    )
}
