import React from 'react';
import Modal from 'react-modal';
import { useDispatch, useSelector } from 'react-redux';
import { _uiCloseModalCollection } from '../../actions/uiActions';

export const PortafolioModal = () => {

    const dispatch = useDispatch();

    const { modalCollection, currentImage } = useSelector(state => state.ui)

    const handleCloseModal = () => {
        dispatch( _uiCloseModalCollection() )
    }

    return (
        <Modal
            isOpen={ modalCollection }
            // style={ customStylesFolio }
            closeTimeoutMS={ 200 }
            onRequestClose={ handleCloseModal }
            className="modal-folio animate__animated animate__fadeIn"
        >
            <div className="modal-folio__content">
                <button onClick={ handleCloseModal }><i className="fas fa-times"></i></button>
                <img src={ currentImage } alt='Jean Victor Rada'/>

            </div>
        </Modal>
    )
}
