import React from 'react';
import { useMediaQuery } from 'react-responsive';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Redirect 
} from 'react-router-dom';
import { AboutScreen } from '../components/about/AboutScreen';
import { HomeScreen } from '../components/home/HomeScreen';
import { PortafolioScreen } from '../components/portafolio/PortafolioScreen';
import { Footer } from '../components/ui/Footer';
import { NavScreen } from '../components/ui/NavScreen';

export const RouterApp = () => {

    const isDesktop = useMediaQuery({
        query: '(min-width: 750px)'
      })

    return (
        <>
            <Router>
                <NavScreen/>
                <div className="container" style={ (!isDesktop) ? ({ paddingTop: '55px' }): ({ paddingTop: '0px' }) }>
                    <Switch>
                        <Route exact path="/about" component={ AboutScreen }/>
                        <Route exact path="/folio" component={ PortafolioScreen }/>
                        <Route exact path="/" component={ HomeScreen }/>

                        <Redirect to="/"/>
                    </Switch>
                </div>
                <Footer/>
            </Router>
        </>
    )
}
