import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { _uiCloseModal } from '../../actions/uiActions';
import { customStyles } from '../../helpers/customStyles';
import Modal from 'react-modal';
import './contact.css';
import { useMediaQuery } from 'react-responsive';
import validator from 'validator';
import { useForm } from '../../hooks/useForm';

Modal.setAppElement('#root');

export const ContactModal = () => {

    const isModal = useMediaQuery({
        query: '(min-width: 500px)'
      })

    const dispatch = useDispatch();
    const { modalContact } = useSelector(state => state.ui);

    const [ formValues, formChange ] = useForm({
        name: '',
        email: '',
        phone: '',
        message: ''
    })

    const {
        name,
        email,
        phone,
        message
    } = formValues;

    const closeModal = () => {
        dispatch( _uiCloseModal() )
    }

    const handleOnSubmit = (e) => {
        e.preventDefault();
        if( name.trim().length <3 ){
            return console.log('name')
        }else if( !validator.isEmail( email ) ){
            return console.log('email')
        }else if( !validator.isMobilePhone( phone)){
            return console.log('phone')
        }
            
        alert(`I'm sorry ${ name }, the service is in maintenance.`)
    }

    return (
        <Modal
            isOpen={modalContact}
            style={customStyles}
            onRequestClose={closeModal}
            closeTimeoutMS={ 200 }
            className='modal-contact'
        >
            <div className={ isModal? 'modal-message__desktop animate__animated animate__fadeInUp': 'modal-message animate__animated animate__fadeInUp' } >
                <div className='modal-button'>
                    <button onClick={ closeModal }>
                        <i className="fas fa-times"></i>
                    </button>
                </div>
                <div className='modal-info'>
                    <div className='modal-info__item'>
                        <h5>Contact</h5>
                        <p>
                            radajean36@gmail.com
                            <br/>
                            Tel: +591 68022274
                        </p>
                    </div>
                    <div className='modal-info__item'>
                        <h5>Based in</h5>
                        <p>
                            La Paz
                            <br/>
                            Bolivia
                        </p>
                    </div>
                </div>
                <form onSubmit={ handleOnSubmit } className='modal-form'>
                    <label htmlFor="name">Name</label>
                    <input value={ name } name="name" onChange={ formChange } type='text' id='name' required/>
                    
                    <label htmlFor="email">Email*</label>
                    <input value={ email } name="email" onChange={ formChange } type='email' id='email' required/>
                    
                    <label htmlFor="phone">Phone</label>
                    <input value={ phone } name="phone" onChange={ formChange } type='text' id='phone'/>
                    
                    <label htmlFor="message">Message</label>
                    <textarea value={ message } name="message" onChange={ formChange } id='message'/>
                    
                    <button>Contact Me</button>
                </form>
            </div>
        </Modal>        
    )
}
